import { Component, OnInit } from '@angular/core';

import { ActivatedRouteSnapshot, ActivatedRoute } from '@angular/router';
import { Model } from 'src/app/api/models/model';
import { RequestService } from 'src/app/api/request.service';
import { UtilService } from 'src/app/services/util.service';
import { DialogService } from 'src/app/api/util/dialog.service';


@Component({
  selector: 'app-game',
  templateUrl: './game.page.html',
  styleUrls: ['./game.page.scss','../../home/games/games.page.scss'],
})
export class GamePage implements OnInit {

  public id : any;
  public gameModel : Model;
  public game : any ;
  private firstLoading = true;

  constructor(
    public route : ActivatedRoute,
    public requesService : RequestService,
    public util : UtilService,
    public dialogService : DialogService, 

  ) {
    this.id = this.route.snapshot.paramMap.get('id');
    this.gameModel = new Model('Game',requesService);

   }

  ngOnInit() {
    this.init();
  }

  async init(event = null){

    if(this.firstLoading){
      await this.dialogService.loadingProgres();
      this.firstLoading = false;
    }
    this.gameModel.api_functionModel(this.id,'dataProfile').subscribe(data => {
      if(data['status'] == 'success')
        this.game = data['data'];
      
      if(event)
        event.target.complete();
      this.dialogService.dimissLoaging();
    })
  }

}
